@import "common/common";
@import "common/bodyCommon";

@font-face {
    font-family: "iconfont";
    src: url('../fonts/iconfont1.eot'); /* IE9*/
    src: url('../fonts/iconfont.eot#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('../fonts/iconfont1.woff') format('woff'), /* chrome, firefox */
    url('../fonts/iconfont1.ttf') format('truetype'), /* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
    url('../fonts/iconfont1.svg#iconfont') format('svg'); /* iOS 4.1- */
}

.login-wrap {
    position: relative;
    background: url("../images/login-bg.jpg");
    background-size: cover;
    font-family: 'Microsoft YaHei';
}

//登录
.signIn-wrap {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);

}

.s-logo {
    height: 37px;
    margin-bottom: 24px;
    background-image: url("../images/s-logo.png");
    background-repeat: no-repeat;
}

.s-main {
    width: 780px;
    padding: 25px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 8px;
}

.input-box {
    display: inline-block;
    margin-left: 20px;
    &:first-child {
        margin-left: 0;
    }
}
.input{
    font-family: 'microsoft yahei';
}

.l-phone {
    width: 300px;
}

.l-pwd {
    //width: 190px;
    width: 150px;
}

.l-submit {
    width: 170px;
    height: 42px;
}

.l-bottom {
    margin-top: 21px;
    @include clearfix;
    color: #fff;

}

a.a-btn {
    display: inline-block;
    margin: 0 7px;
    text-decoration: underline;
    color: #fff;
}

.l-download {
    display: inline-block;
    margin-left: 15px;
    label {
        display: inline-block;
        margin-right: 10px;
    }
}

.code-wrap {
    position: absolute;
    display: none;
    top: 35px;
    left: 50%;
    padding: 10px;
    transform: translate3d(-50%, 0, 0);
    background: #fff;
    &:after {
        content: "";
        display: inline-block;
        position: absolute;
        top: -8px;
        left: 50%;
        transform: translate3d(-50%, 0, 0);
        @include triangle(top, 8px, #fff);
    }
    .icon:hover & {
        display: block;
    }
}

.code {
    @include icon;
    width: 152px;
    height: 152px;
    background-image: url("../images/down-code.jpg");
    background-size: contain;
}

.icon {
    position: relative;
    display: inline-block;
    margin: 0 10px;
    //height: 20px;
    cursor: pointer;
    i {
        &:after {
            font-family: iconfont;
            font-style: normal;
            font-size: 25px;
            color: #fff;
            line-height: 1;
        }
        &.android:after {
            content: "\e655";
        }
        &.ios:after {
            content: "\e656";
        }
    }
}


.signUp-wrap {
    padding: 30px;
    background: #f5f5f5;
    &:before {
        content: "";
        position: absolute;
        top: 2px;
        left: 2px;
        bottom: 2px;
        right: 2px;
        border: 2px solid #252834;
    }
    .active & {
        animation-name: layerDown;
        animation-duration: 0.5s;
        animation-timing-function: ease-in-out;
    }
}

.signUp-close {
    position: absolute;
    top: 0;
    right: 0;
    width: 60px;
    height: 60px;
    text-align: center;
    line-height: 60px;
    overflow: hidden;
    cursor: pointer;
    &:before {
        content: "";
        position: absolute;
        z-index: 0;
        top: 0;
        left: 0;
        width: 200%;
        height: 100%;
        transform: rotate(45deg) translate3d(-26px, -8px, 0);
        background: #252834;
    }
    i {
        color: #fff;
        position: absolute;
        font-size: 35px;
        top: -7px;
        left: 30px;
    }
}

.signUp-main {
    padding: 10px 30px 20px;
}

.s-title {
    margin-bottom: 30px;
    text-align: center;
    font-size: 22px;
    font-weight: bold;
}

.s-box {
    width: 360px;
    margin: 0 auto;
}

.s-input-box {
    position: relative;
    margin-bottom: 20px;
}

.yzm-input {
    padding-right: 142px;
}

